import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Course } from './course';
import { Observable } from 'rxjs/internal/Observable';



@Injectable({
  providedIn: 'root'
})
export class CourseServiceService {

  private courseUrl: string;

  constructor(private http: HttpClient) {
    this.courseUrl = 'http://15.206.94.109:8040/course-api';
    //this.courseUrl = 'http://localhost:8040/course-api';
  
   }

   public findAll(): Observable<Course[]> {
    return this.http.get<Course[]>(`${this.courseUrl}/get-all-courses`);
  }

  public save(course: Course) {
    return this.http.post<Course>(`${this.courseUrl}/insert-course`, course);
  }

  public update(course:Course , id : number ){
    return this.http.put(`${this.courseUrl}/update-course-by-id/${course.courseId}`,course);
  }

  public delete(id : number){
   return this.http.delete(`${this.courseUrl}/delete-course-by-id/${id}`)
}


}
